.output {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  flex: 1 1 0%;
  padding: 0 1.5rem;
  overflow: hidden;
}

.output-scroll {
  width: 100%;
  max-height: 100%;
  padding: 1.5rem 1.5rem 0 1.5rem;
  overflow: auto;
  scroll-behavior: smooth;
}

@media (max-width: 767px) {
  .output {
    padding: 3.75rem 1.5rem 0; /* 60px 24px */
  }
}